<template>
  <div class="search">
    <van-nav-bar title="商品搜索" left-arrow @click-left="$router.go(-1)" />

    <van-search v-model="keyword" show-action @keyup.enter="searchFn(keyword)" placeholder="请输入搜索关键词" clearable>
      <template #action>
        <div  @click="searchFn(keyword)">搜索</div>
      </template>
    </van-search>

    <!-- 搜索历史 -->
    <div v-if="history.length > 0" class="search-history">
      <div class="title">
        <span>最近搜索</span>
        <van-icon @click="resetFn" name="delete-o" size="16" />
      </div>
      <div  class="list">
        <div v-for="item in history" :key="item" class="list-item" @click="searchFn(item)">{{ item }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { setKeyword, getKeyword, removeKeyword } from '@/utils/storage'
// import GoodsItem from '@/components/GoodsItem.vue'
export default {
  name: 'SearchIndex',
  data () {
    return {
      keyword: '',
      // history: ['炒锅', '电视', '冰箱', '手机']
      history: getKeyword()
    }
  },
  components: {
  },
  methods: {
    searchFn (key) {
      // 判断历史中是否有该关键词
      if (!this.history.includes(key)) {
        // 并且判定输入的是否是空
        if (this.keyword.trim() === '') return
        // 没有就新增
        this.history.unshift(key)
      } else {
        // 有的话筛选出此关键词，然后再新增
        this.history = this.history.filter(item => item !== key)
        this.history.unshift(key)
      }
      setKeyword(this.history)

      // 跳转到搜索详情
      this.$router.push(`/list?keyword=${key}`)
      this.keyword = ''
      console.log('搜索了：' + key)
    },
    resetFn () {
      this.history = []
      removeKeyword()
    }

  }
}
</script>

<style lang="less" scoped>
.search {
  .searchBtn {
    background-color: #fa2209;
    color: #fff;
  }

  ::v-deep .van-search__action {
    background-color: #c21401;
    color: #fff;
    padding: 0 20px;
    border-radius: 0 5px 5px 0;
    margin-right: 10px;
  }

  ::v-deep .van-icon-arrow-left {
    color: #333;
  }

  .title {
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
  }

  .list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    padding: 0 10px;
    gap: 5%;
  }

  .list-item {
    width: 30%;
    text-align: center;
    padding: 7px;
    line-height: 15px;
    border-radius: 50px;
    background: #fff;
    font-size: 13px;
    border: 1px solid #efefef;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-bottom: 10px;
  }
}</style>
